#mainEpisodeVideo {
  position: relative;
  cursor: pointer;

  .shareButtons.only-above-980 {
    display: none;

    @media(min-width: 980px) {
      position: absolute;
      bottom: 20px;
      right: 20px;
      display: block;
      z-index: 10;

      @include transform(translateX(100%)); //

      button {
        margin-bottom: 0;
      }
    }
  }

  //@media(min-width: 980px) {
  //  background: #424242;
  //}

  @import '../partials/bigPlayButton';

  #videoContainer {
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    position: relative;

    iframe {
      background-color: #000;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      border-width: 0;
    }
  }

  #backgroundImage {
    opacity: 1;
    position: absolute;
    z-index: 10;
    display: block;

    body.touch & {
      pointer-events: none;
    }
  }

}