.contentBlock {

  max-width: 1300px;
  position: relative;

  .separator {
    height: 1px;
    background-color: #666;
    margin: 0 20px;
  }

  h4.sectionHeading {
    color: white;
    width: auto;
    padding: 20px 20px 0 20px;
    position: relative;
    font-size: 16px;

    font-weight: 600;

    img,
    span {
      display: block;
    }

    img {
      margin: 10px 0;
    }

    @media(min-width: 980px) {
        font-size: 18px;
    }
  }

  .contentBlock-inner {
    padding: 20px;
    font-size: 0;

    h2 {
      font-size: 16px;
      line-height: 1.2em;
      font-weight: 500;
      margin-top: 4px;
      color: $accent-color;

      @media(min-width: 980px) {
        font-size: 18px;
      }
    }

    p {
      font-size: 14px;
      margin: 14px 0;

      @media(min-width: 980px) {
        font-size: 13px;
      }

      @media(min-width: 1300px) {
        font-size: 14px;
      }

      &.half-width {
        width: auto;
        @media(min-width: 740px) {
          width: 47%;
        }
      }

      &.no-margin-top {
        margin-top: 0 !important;
      }
    }

  }

  @media(min-width: 980px) {
    //      max-width: 620px;
    margin: 0 auto;

    h4.sectionHeading {
      width: 180px;
      position: absolute;
    }

    .contentBlock-inner {
      padding-left: 220px;
    }
  }


  @media(min-width: 980px) {
    max-width: none;
    margin: 0 auto;
  }

}
